const Base64=require('js-base64')
const axios=require('axios')
const qs=require('qs')
import store from '../store'

var header={Version: "3.1.0"};

function getURL(flag,url){
  if(process.env.NODE_ENV == 'production'){
    if(flag == "my")
      return "https://szone-my.7net.cc"+url;
    if(flag == "api")
      return "https://szone-api.7net.cc"+url; 
    if(flag == "score")
      return "https://szone-score.7net.cc"+url;          
  } else {
    if(flag == "my")
      return "/my"+url;
    if(flag == "api")
      return "/api"+url; 
    if(flag == "score")
      return "/score"+url;      
  }
}

async function queryUserInfo(){
  let token = store.getters.getToken();
  let res =  await axios.get(getURL("my","/userInfo/GetUserInfo"),
        {headers:{Version: "3.1.0",'Token':token}});
  if(res.status == 200 && res.data.status == 200){
        store.commit("setUserInfo",res.data.data);
  } else {
    userLogout();
  }
  return res;
}

async function userLogin(account,password){
  let user={
    userCode:account,
    password:Base64.encode(password),
  };
  let res = await axios.post(getURL("my","/login"), qs.stringify(user),{headers:header});
  if(res.status == 200  && res.data.status == 200){
      store.commit("setToken",res.data.data.token);
      res = queryUserInfo();
  } 
  return res; 
}

async function getSMSCode(account){
  let user={
    userCode:Base64.encode(account),
  };

  let res = await axios.post(getURL("my","/login/sendsms"),qs.stringify(user),{headers:header});

  if(res.status == 200 && res.data.status==200){
    store.commit("setSMSToken",res.data.data.token);
  }
  return res;   
}

async function phoneLogin(phone,sms){
  let user={
    userCode:phone,
    verifyCode:sms,
    token:store.getters.getSMSToken(),
  };

  let res = await axios.post(getURL("my","/login/entry"), qs.stringify(user),{headers:header});
  console.log(res);  
  if(res.status == 200 && res.data.status == 200){
      store.commit("setToken",res.data.data.token);
      res = queryUserInfo();
  } 
  return res; 
}

function userLogout(){
  store.commit("clear");
}

async function queryExamCount(){
  let userData = store.getters.getUserInfo();
  let data = {
    studentName:userData.studentName,
    schoolGuid:userData.schoolGuid,
  };
  let res = await axios.get(getURL("score","/exam/getExamCount?")+qs.stringify(data),
    {headers:{Version: "3.1.0",'Token':store.getters.getToken()}});
}

async function queryExamList(row,index) {
  let userData = store.getters.getUserInfo();

  let data={
    grade:userData.grade,
    rows:row,
    startIndex:index,
    schoolGuid:userData.schoolGuid,
    studentName:userData.studentName,
  };

  let res = await axios.get(getURL("score","/exam/getClaimExams?")+qs.stringify(data),
    {headers:{Version: "3.1.0",'Token':store.getters.getToken()}});

  if(res.status == 200 && res.data.status == 401){
    userLogout();
  }
  
  return res; 
}

async function querySubjectInfo(para){
  let data={
    examGuid:para.examGuid,
    studentCode:para.studentCode,
    schoolGuid:para.schoolGuid,
    grade:para.gradeCodeString,
    ruCode:para.ruCode,
  };

  let header={Version: "3.1.0",'Token':store.getters.getToken(),'Content-Type': "application/x-www-form-urlencoded"};

  let res = await axios.post(getURL("score","/Question/Subjects"),qs.stringify(data),{headers:header});

  if(res.status == 200 && res.data.status==401){
    userLogout();
  }
  
  return res; 
}

async function querySubjectGrade(para,exam,sub){
  let userData = store.getters.getUserInfo(); 
  let data={
    schoolGuid:para.schoolGuid,
    studentCode:para.studentCode,
    studentName:userData.studentName,
    grade:para.gradeCodeString,
    ruCode:para.ruCode,

    examGuid:para.examGuid,
    examSchoolGuid:exam.schoolGuid,
    examType:exam.examType,

    subject:sub.km,
    compareClassAvg:sub.compareClassAvg,
  };

  let header={Version: "3.1.0",'Token':store.getters.getToken(),'Content-Type': "application/x-www-form-urlencoded"};

  let res = await axios.post(getURL("score","/Question/SubjectGrade"),qs.stringify(data),{headers:header});
 
  if(res.status == 200 && res.data.status==401){
    userLogout();
  }
  
  return res; 
}

async function queryAnswerCard(para,exam,sub){
  let userData = store.getters.getUserInfo();
  let data={
    asiresponse:sub.question.asiresponse,
    examGuid:para.examGuid,
    srcSubject:sub.srcKM,
    studentName:userData.studentName,
    ruCode:para.ruCode,
    scoreStatus: "-1",
    schoolGuid:para.schoolGuid,
  };

  let header={Version: "3.1.0",'Token':store.getters.getToken(),'Content-Type': "application/x-www-form-urlencoded"};

  let res = await axios.post(getURL("score","/Question/AnswerCardUrl"),qs.stringify(data),{headers:header});

  if(res.status == 200 && res.data.status==401){
    userLogout();
  }

  return res; 
}


export default {
  queryUserInfo,
  getSMSCode,
  userLogin,
  phoneLogin,
  userLogout,
  queryExamCount,
  queryExamList,
  querySubjectInfo,
  querySubjectGrade,
  queryAnswerCard,
};



