import Vue from 'vue'
import VueX from 'vuex'

Vue.use(VueX)

export default new VueX.Store({
    state:{
        token:'',
        smsToken:'',
        userInfo:{},
    },
    mutations:{
        setToken:(state,token)=>{
            state.token = token;
            localStorage.setItem("token",token);
        },
        setUserInfo:(state,info)=>{
            state.userInfo = info;
            localStorage.setItem("userInfo",JSON.stringify(info));            
        },
        setSMSToken:(state,token)=>{
            state.smsToken = token;
            localStorage.setItem("smsToken",token);
        },
        clear:(state)=>{
            state.token="";
            state.smsToken ="";
            state.userInfo ={};
            localStorage.setItem("token","");
            localStorage.setItem("userInfo",{});   
            localStorage.setItem("smsToken","");                                   
        },
    },
    getters:{
        getToken:(state)=>()=>{
            if(state.token == "")
                state.token = localStorage.getItem("token");
            return state.token;
        },
        getUserInfo:(state)=>()=>{
            if(state.userInfo.studentName == undefined){
                state.userInfo = JSON.parse(localStorage.getItem("userInfo"));
            }
            return state.userInfo;
        },
        getSMSToken:(state)=>()=>{
            if(state.smsToken == "")
                state.smsToken = localStorage.getItem("smsToken");
            return state.smsToken;
        },
    },
    actions:{

    },
    modules:{

    }
})


