// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import {PullRefresh,loading,Col,Row,Tab,Tabs,ImagePreview,Uploader,RadioGroup, Radio,Picker,Dialog,Tag,Button, Toast, NavBar,Popup,Cell,Icon,CellGroup,List,Search,ContactCard, ContactList, ContactEdit,Field} from 'vant'
import axios from 'axios'
import {Image as VImage} from 'vant'
//import loading from '@/components/loading.js'
import zone from '@/components/zone.js'
import store from '@/store'
import qs from 'qs'
import VueTouch from 'vue-touch'
import vueEsign from 'vue-esign'
import '@vant/touch-emulator'
Vue.use(PullRefresh)
Vue.use(loading)
Vue.use(VImage)
Vue.use(vueEsign)
Vue.use(ImagePreview)
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Col)
Vue.use(Row)
Vue.use(Uploader)
Vue.use(Picker)
Vue.use(Dialog)
Vue.use(Field)
Vue.use(Tag)
Vue.use(Popup)
Vue.use(Toast)
Vue.use(Button)
Vue.use(NavBar)
Vue.use(Cell)
Vue.use(CellGroup)
Vue.use(List)
Vue.use(Search)
Vue.use(Icon)
Vue.use(RadioGroup)
Vue.use(Radio)
Vue.use(ContactCard)
Vue.use(ContactList)
Vue.use(ContactEdit)
Vue.use(VueTouch)

Vue.config.productionTip = false
Vue.prototype.http = axios
Vue.prototype.zone = zone
Vue.prototype.qs = qs
Vue.prototype.user = {id:0,name:'',token:0,icon:''}
axios.defaults.baseURL = process.env.BASE_API

new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>',
})



